      setLastDate(`${year ? `${year}Âπ¥` : ''}${day ? `${day}Êó•` : ''}${hour ? `${String(hour).padStart(2, '0')}ÊôÇÈñì` : ''}${min ? `${String(min).padStart(2, '0')}ÂàÜ` : ''}${sec}Áßí„ÄÇ`);

